/****
 *
 * body
 *
****/

.two-columns {
	display: flex;
	flex-wrap: wrap;

	.column {
		width: calc(50% - 16px);
		margin-right: 3.2rem;

		&:nth-child(2) {
			margin-right: 0;
		}
	}
}

.three-columns {
	.column {
		display: inline-block;
		width: calc(33.33% - 20px);
		margin-right: 25px;

		&:nth-child(3n + 3) {
			margin-right: 0;
		}
	}
}

/****
 *
 * Valign-wrapper Style
 *
 ****/
.valign-wrapper {
	display: flex;
	align-items: center;
}

/****
 *
 * helper classes
 *
****/

.center {
	text-align: center;
}
.float-right {
	float: right;
}

.float-left {
	float: left;
	padding-right: 10px;
}

/****
 *
 * breadcrumbs
 *
****/
#breadcrumbs {
	color: map-get($map: $base_colors, $key: breadcrumb-color);
	text-transform: uppercase;
	margin-bottom: 23px;

	@include font-size(1.4);

	span {
		@include font-size(1.8);
	}

	span.breadcrumb_last {
		@include font-size(1.4);

		font-weight: 500;
		margin-left: 10px;
	}

	a,
	&:visited {
		color: map-get($map: $base_colors, $key: breadcrumb-link-color);
		font-weight: 700;
		margin: 0 10px;
		border-bottom: 1px solid map-get($map: $base_colors, $key: breadcrumb-border-color);

		@include font-size(1.4);

		&:hover {
			border-bottom: 1px solid map-get($map: $base_colors, $key: breadcrumb-border-color-hover);
		}
	}
}

.single-post #breadcrumbs span span span span span span {
	color: map-get($map: $base_colors, $key: breadcrumb-span-color);
}

.single-post #breadcrumbs span span span span span span a {
	color: map-get($map: $base_colors, $key: breadcrumb-link-color);
}

.single-post #breadcrumbs {
	span.breadcrumb_last {
		display: none;
	}
}

/****
 *
 * buttons
 *
****/

a.button,
a.button:visited {
	box-sizing: border-box;
	position: relative;
	display: inline-block;
	overflow: hidden;
	padding: 12px 34px;
	text-decoration: none;
	color: map-get($map: $colors, $key: white);
	background-color: map-get($map: $colors, $key: red);
	border: 0;
	transition: all 0.35s ease-in-out;
	@include font-size(2);
	margin-right: 15px;
	@include border-radius(2px);
	@include font(bt, reg);

	&:last-child {
		margin-right: 0;
	}

	&:hover {
		background-color: map-get($map: $colors, $key: bwhale);
	}
	&.big {
		padding: 15px 45px;
		font-size: 26px;
		line-height: 1;
	}
	&.blue-btn {
		background-color: map-get($map: $colors, $key: bwhale);
		&:hover {
			background-color: map-get($map: $colors, $key: red);
		}
	}
	&.arrow-btn {
		background-color: transparent;
		font-size: 20px;
		padding: 6px 53px 6px 10px;
		color: map-get($map: $colors, $key: bwhale);
		background-image: url(../img/blue-right-arrow.svg);
		background-repeat: no-repeat;
		background-position: center right 10px;
		@include font(bt, reg);
		&:after {
			content: "";
			width: 40px;
			height: 42px;
			border: 2px solid map-get($map: $colors, $key: bwhale);
			position: absolute;
			right: 0;
			top: 0;
			border-radius: 5px;
			transition: all 0.4s ease-in-out;
		}
		&:hover {
			&:after {
				width: 100%;
			}
		}
		&.fill {
			background-image: url(../img/white-right-arrow.svg);
			color: map-get($map: $colors, $key: white);
			&:after {
				background-color: map-get($map: $colors, $key: bwhale);
				width: 100%;
				z-index: -1;
			}
			&:hover {
				color: map-get($map: $colors, $key: bwhale);
				&::after {
					width: 40px;
				}
			}
		}
	}
}

a.wp-block-button__link,
a.wp-block-button__link:visited,
a.wp-block-button__link {
	box-sizing: border-box;
	position: relative;
	display: inline-block;
	overflow: hidden;
	padding: 11px 30px;
	text-decoration: none;
	color: map-get($map: $base_colors, $key: theme-btn-color);
	background-color: map-get($map: $base_colors, $key: theme-btn-bgcolor);
	border: 2px solid map-get($map: $base_colors, $key: theme-btn-border-color);
	transition: all 0.35s ease-in-out;
	margin-right: 9px;

	@include font(bt, reg);
	@include font-size(1.6);
	@include border-radius(100px);

	&:hover {
		background-color: map-get($map: $base_colors, $key: theme-btn-bgcolor-hover);
		border: 2px solid map-get($map: $base_colors, $key: theme-btn-border-color-hover);
		color: map-get($map: $base_colors, $key: theme-btn-color-hover);
	}

	&.has-background {
		border: none;

		&:hover {
			border: none;
			color: unset;
		}
	}

	&.has-text-color.has-background {
		border: none;

		&:hover {
			border: none;
		}
	}
}

/****
 *
 * WordPress buttons
 *
****/

a.wp-block-button__link,
a.wp-block-button__link:visited,
.wp-block-button__link {
	&:hover {
		// border: 2px solid map-get($map: $base_colors, $key: wp-btn-border-color);
		// color: map-get($map: $base_colors, $key: wp-btn-color);
		// background-color: map-get($map: $base_colors, $key: wp-btn-bgcolor);
	}
}

.wp-block-button.is-style-outline {
	a.wp-block-button__link,
	a.wp-block-button__link:visited {
		border: 2px solid map-get($map: $base_colors, $key: wp-outline-btn-border-color);

		&:hover {
			background-color: map-get($map: $base_colors, $key: wp-outline-btn-bgcolor);
			color: map-get($map: $base_colors, $key: wp-outline-btn-color);
		}

		&.has-text-color,
		&.has-background {
			&:hover {
				background-color: unset;
				color: unset;
			}
		}

		&.has-text-color.has-background {
			border: none;

			&:hover {
				border: none;
			}
		}
	}
}

.no-visible {
	visibility: hidden;
}

/****
 *
 * 404 page
 *
****/
.not-found {
	text-align: center;
	padding: 50px 0;
}

.error {
	text-align: center;
	margin: 2rem 0;
	width: 100%;

	.menu {
		margin: 0;
	}

	ul li {
		display: inline-block;
		margin: 0 0 0 20px;
		font-weight: 700;
		padding: 0;

		&::before {
			display: none;
		}

		a {
			padding: 1rem;
			border-bottom: 0px;

			&:hover {
				color: map-get($map: $base_colors, $key: error-menu-color);
			}
		}

		ul {
			display: none;
		}
	}
}

.form-404 {
	margin: 5rem auto 0;
	max-width: 720px;

	p {
		margin-bottom: 2rem;
	}
}

.flex {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: center;
}
.flex-ctn,
.flexarea {
	display: flex;
	flex-wrap: wrap;
}
.image-at-right {
	flex-direction: row-reverse;
}
